/* ContactMe.css */
.message-board {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.message-form {
  margin-bottom: 20px;
}

.message-form input,
.message-form textarea,
.message-form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
}

.message-list .message {
  background-color: #f9f9f9;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}
